
/**
 * Hreinsa bÃ¶rn Ãºr elementi
 *
 * @param {object} element Element sem Ã¡ aÃ° hreinsa bÃ¶rn Ãºr
 */
export function empty(element) {
  while (element.firstChild) {
    element.removeChild(element.firstChild);
  }
}

/**
 * BÃºa til element og aukalega setja bÃ¶rn ef send meÃ°
 *
 * @param {string} name Nafn Ã¡ element
 * @param  {...any} children BÃ¶rn fyrir element
 */
export function el(name, ...children) {
  const element = document.createElement(name);

  if (Array.isArray(children)) {
    children.forEach((child) => {
      if (typeof child === 'string') {
        element.appendChild(document.createTextNode(child));
      } else if (child) {
        element.appendChild(child);
      }
    });
  }

  return element;
}

/**
* Skilar tÃ¶lu af handahÃ³fi Ã¡ bilinu [min, max]
*/
export function randomNumber(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

/**
 * Skilar dagsetningu af handahÃ³fi
 */
export function getRandomDate() {
  const start = new Date(1995, 6, 16);
  const end = new Date();
  const randomDate = new Date(start.getTime() + Math.random()*(end.getTime() - start.getTime()));
  return randomDate.toISOString().slice(0, 10);
}