/**
 * SÃ¦kir og vistar Ã­ localStorage
 */

// Fast sem skilgreinir heiti Ã¡ lykli sem vistaÃ° er undir Ã­ localStorage
const LOCALSTORAGE_KEY = 'favourite_spacephotos';

/**
 * SÃ¦kir gÃ¶gn Ãºr localStorage. SkilaÃ° sem lista Ã¡ forminu:
 * [{ type, mediaUrl, text, title },
 *  { type, mediaUrl, text, title },
 *  ...,
 *  { type, mediaUrl, text, title }]
 *
 * @returns {array} fylki af myndum eÃ°a tÃ³ma fylkiÃ° ef ekkert vistaÃ°.
 */
export function load() {
  const data = localStorage.getItem(LOCALSTORAGE_KEY);
  return JSON.parse(data);
}

/**
 * VistaÃ°ar myndir meÃ° texta.
 *
 * @param {string} type annaÃ° hvort image eÃ°a video
 * @param {string} mediaUrl URL Ã¡ myndinni/myndbandinu.
 * @param {string} text texti fyrir myndina/myndbandiÃ°.
 * @param {string} title titill fyrir myndina/myndbandiÃ°.
 */
export function save(type, imageUrl, text, title) {

  let favorites = load();

  if(favorites == null){
    favorites = [];
  }
    
  favorites.push({type, imageUrl, text, title});

  const data = JSON.stringify(favorites);

  console.log(data);
  localStorage.setItem(LOCALSTORAGE_KEY, data);
}

/**
 * Hreinsar allar myndir Ãºr localStorage
 */
export function clear() {
  localStorage.removeItem(LOCALSTORAGE_KEY);
}
