export default async function getRandomImage() {

    const date = getRandomDate();

    const result = await fetch(`${URL}?api_key=${API_KEY}&date=${date}`);

    const {
        url: imageUrl,
        explanation: text,
        title: title
    } = await result.json();

    return { imageUrl, text, title };
}
